import logo from "./logo.svg";
import Header from "./components/Header";
import CategoryBar from "./components/CategoryBar";
import Feed from "./components/Feed";
import Footer from "./components/Footer";
import { useState } from "react";
import "./App.css";
import SearchBar from "./components/SearchBar";

function App() {
  const [isFunnySelected, setIsFunnySelected] = useState(false);
  const [isSadSelected, setIsSadSelected] = useState(false);
  const [isCrazySelected, setIsCrazySelected] = useState(false);

  const [isPageScroll, setIsPageScroll] = useState(false);

  window.onscroll = () => {
    window.scrollY > 300 ? setIsPageScroll(true) : setIsPageScroll(false);
    console.log(window.scrollY);
  };

  return (
    <div className="App">
      <div className="header-fixed">
        <Header />
        <SearchBar />
      </div>
      <CategoryBar
        isFunnySelected={isFunnySelected}
        setIsFunnySelected={setIsFunnySelected}
        isSadSelected={isSadSelected}
        setIsSadSelected={setIsSadSelected}
        isCrazySelected={isCrazySelected}
        setIsCrazySelected={setIsCrazySelected}
      />
      <Feed
        isFunnySelected={isFunnySelected}
        setIsFunnySelected={setIsFunnySelected}
        isSadSelected={isSadSelected}
        setIsSadSelected={setIsSadSelected}
        isCrazySelected={isCrazySelected}
        setIsCrazySelected={setIsCrazySelected}
      />

      <Footer />
      {isPageScroll && (
        <button
          class="scroll-up"
          onClick={() => window.scrollTo({ top: 0, behavior: "smooth" })}
        >
          Back To Top
        </button>
      )}
    </div>
  );
}

export default App;
