import "./CategoryBar.css";
import CategoryButton from "./CategoryButton";

import { useState } from "react";

const CategoryBar = ({
  isFunnySelected,
  setIsFunnySelected,
  isSadSelected,
  setIsSadSelected,
  isCrazySelected,
  setIsCrazySelected,
}) => {
  return (
    <div className="category-container">
      <CategoryButton
        title="Funny"
        onClick={() => {
          isFunnySelected
            ? setIsFunnySelected(false)
            : setIsFunnySelected(true);
        }}
        addActiveClass={isFunnySelected && "active"}
      />

      <CategoryButton
        title="Sad"
        onClick={() => {
          isSadSelected ? setIsSadSelected(false) : setIsSadSelected(true);
        }}
        addActiveClass={isSadSelected && "active"}
      />

      <CategoryButton
        title="Crazy"
        onClick={() => {
          isCrazySelected ? setIsCrazySelected(false) : setIsCrazySelected(true);
        }}
        addActiveClass={isCrazySelected && "active"}
      />
    </div>
  );
};

export default CategoryBar;
