import "./Feed.css";
import Card from "./Card";
import { MemeInfo } from "./MemeInfo";

import { useState } from "react";

const Feed = ({ isFunnySelected, isSadSelected, isCrazySelected }) => {
  let popularArray = MemeInfo.filter((meme) => meme.topPick === true);
  let funnyMemeArray = MemeInfo.filter((funnyMeme) =>
    funnyMeme.category.includes("funny")
  );
  let sadMemeArray = MemeInfo.filter((sadMeme) =>
    sadMeme.category.includes("sad")
  );
  let crazyMemeArray = MemeInfo.filter((crazyMeme) =>
    crazyMeme.category.includes("crazy")
  );

  const originalFeedToggle = () => {
    if (!isFunnySelected && !isSadSelected && !isCrazySelected) {
      return MemeInfo.map((meme) => (
        <Card title={meme.title} image={meme.image} />
      ));
    }
  };

  const popularToggle = () => {
    if (!isFunnySelected && !isSadSelected && !isCrazySelected) {
      return (
        <div className="popular-container">
          <div className="icon-heading-wrap">
            <div>
              <svg
                width="25"
                height="20"
                viewBox="0 0 25 20"
                xmlns="http://www.w3.org/2000/svg"
                className="icon-heading-wrap-icon"
              >
                <defs>
                  <linearGradient
                    x1="5.615%"
                    y1="77.472%"
                    x2="100%"
                    y2="26.124%"
                    id="trending"
                  >
                    <stop stop-color="#3191FF" offset="0%"></stop>
                    <stop stop-color="#0CF" offset="100%"></stop>
                  </linearGradient>
                </defs>
                <path
                  d="M25.333 4.635l-6.45-.032a.47.47 0 00-.471.468l.004 1.575.008.085a.47.47 0 00.462.383h2.94l-7.544 8.101-3.878-3.125a1.119 1.119 0 00-1.631-.009l-7.584 7.73a1 1 0 00-.002 1.4l.288.295a1 1 0 001.431 0L9.652 14.6l3.782 3.042.093.1c.442.442.964.541 1.498.145l8.43-8.998v3.103c0 .25.197.456.446.468l1.407.069a.47.47 0 00.491-.446V5.104a.47.47 0 00-.466-.469z"
                  fill="url(#trending)"
                  fill-rule="nonzero"
                  transform="rotate(-5 -21.505 23.157)"
                ></path>
              </svg>
            </div>
            <h1>Popular</h1>
          </div>
          <div className="card-container">
            {popularArray.map((popular) => (
              <Card title={popular.title} image={popular.image} />
            ))}
          </div>
        </div>
      );
    }
  };

  return (
    <div className="feed">
      {popularToggle()}

      <div className="icon-heading-wrap">
        <div>
          <svg
            width="22"
            height="25"
            viewBox="0 0 22 25"
            xmlns="http://www.w3.org/2000/svg"
            className="icon-heading-wrap-icon"
          >
            <defs>
              <linearGradient
                x1="7.986%"
                y1="100%"
                x2="92.014%"
                y2="0%"
                id="stories"
              >
                <stop stop-color="#6157FF" offset="0%"></stop>
                <stop stop-color="#93F" offset="100%"></stop>
              </linearGradient>
            </defs>
            <path
              d="M20.25 25a.75.75 0 110 1.5H7.75a.75.75 0 110-1.5h12.5zm2-3a.75.75 0 110 1.5H5.75a.75.75 0 110-1.5h16.5zm-.134-19.5C23.709 2.5 25 3.719 25 5.222v12.556c0 1.503-1.291 2.722-2.884 2.722H5.884C4.291 20.5 3 19.281 3 17.778V5.222C3 3.719 4.291 2.5 5.884 2.5zm-.215 2.2H6.099c-.496 0-.899.371-.899.83v11.94c0 .459.403.83.9.83h15.8c.497 0 .9-.371.9-.83V5.53c0-.459-.403-.83-.9-.83z"
              transform="translate(-3 -2)"
              fill="url(#stories)"
              fill-rule="nonzero"
            ></path>
          </svg>
        </div>
        <h1>Feed</h1>
      </div>

      <div className="card-container">
        {originalFeedToggle()}

        {isFunnySelected &&
          funnyMemeArray.map((funnyMeme) => (
            <Card title={funnyMeme.title} image={funnyMeme.image} />
          ))}

        {isSadSelected &&
          sadMemeArray.map((sadMeme) => (
            <Card title={sadMeme.title} image={sadMeme.image} />
          ))}

        {isCrazySelected &&
          crazyMemeArray.map((crazyMeme) => (
            <Card title={crazyMeme.title} image={crazyMeme.image} />
          ))}
      </div>
    </div>
  );
};

export default Feed;
